    intro:
      "انحلال شرکت یکی از مراحل مهم حقوقی است که ممکن است به دلایل مختلف اتفاق بیفتد. آشنایی با مراحل قانونی آن، از بروز مشکلات و جریمه‌های احتمالی جلوگیری می‌کند.",
    content:
      "انحلال شرکت در ایران طبق قانون تجارت دارای مراحل مشخصی است. اولین گام، تصمیم مجمع عمومی فوق‌العاده است که با حضور شرکا و تصویب اکثریت، انحلال شرکت اعلام می‌شود. صورت‌جلسه این مجمع باید تنظیم و به اداره ثبت شرکت‌ها ارائه شود. پس از تصویب انحلال، شرکت موظف است تمامی بدهی‌ها و تعهدات مالی خود را تسویه کند. این شامل پرداخت حقوق کارکنان، بدهی‌های مالیاتی و بدهی به تأمین‌کننده‌ها است. مرحله بعدی، انتشار آگهی انحلال در روزنامه رسمی و کثیرالانتشار است تا اطلاع‌رسانی عمومی انجام شود و طلبکاران فرصت ارائه مطالبات خود را داشته باشند. در ادامه، شرکت باید اموال و دارایی‌های خود را ارزیابی و تقسیم کند. این مرحله تحت نظارت هیئت تصفیه انجام می‌شود تا همه دارایی‌ها به طور قانونی بین شرکا تقسیم شود. در صورت وجود اختلاف یا دعاوی، هیئت تصفیه می‌تواند مدارک و قراردادها را بررسی و نسبت به رفع تعارضات اقدام کند. در نهایت، با اتمام تمامی مراحل، اداره ثبت شرکت‌ها انحلال رسمی شرکت را ثبت کرده و گواهی انحلال صادر می‌کند. مشاوره با وکیل یا موسسات حقوقی معتبر مانند «ثبت جاوید» می‌تواند روند انحلال را سریع‌تر و قانونی‌تر کند و از مشکلات آینده جلوگیری نماید.",
